export default {
	data() {
		return {
			dataInfo:null,
		};
	},
	onLoad:function(e){
		console.log(e)
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.id,
			method: 'GET',
			data: {},
			success: res => {
				this.dataInfo=res.data;
			},
			fail: () => {},
			complete: () => {
				uni.hideLoading();
			}
		});
	}
}
